import React, { Component } from "react";
import { Swiper } from "antd-mobile-v5";
import "./index.less";

const pageSize = 4;

const mockData = [
  [{ key: 1 }],
  [{ key: 2 }],
  [{ key: 3 }],
  [{ key: 4 }],
  [{ key: 5 }],
  [{ key: 6 }],
  [{ key: 7 }],
];

// http://10.15.110.25:18880/storage_area/form/1234567890/96a9c04cc4f44607af697d64ed5c8c4a.png
export default class Main extends Component {
  state = {
    id: "",
    number: 1,
  };

  componentDidMount() {
    //
    //dom id
    this.props.mainInit && this.props.mainInit(this);
    this.initComData();
    console.log("==========================");
    console.log("this.props", this.props);
    console.log("==========================");
  }

  initComData = () => {
    const { customConfig } = this.props;
    customConfig.number && this.setState({ number: customConfig.number });
  };

  Event_Center_getName() {
    return this.state.id;
  }

  /**
   * window.eventCenter?.triggerEvent
   * @param {String} eventName 
   * @param {Array} payload 
   *
   */
  triggerEvent = (eventName, payload) => {
    const componentId =
      this.props.componentId || this.props?.customConfig.componentId;
    componentId &&
      window.eventCenter?.triggerEvent(
        componentId,
        eventName,
        //payloadobject
        payload
      );
  };
  handleClick = () => {
    console.log(this);
    this.triggerEvent("click", { value: "123" });
  };

  renderItem = (index) => {
    const { data = [], history = {} } = this.props;

    const dataSource = data.slice(
      index * pageSize,
      index * pageSize + pageSize
    );

    const results = dataSource.map((i) => {
      if (!i) return null;
      const pic = i.find((j) => j?.label === "");
      const link = i.find((j) => j?.label === "");
      const title = i.find((j) => j?.label === "");
      return (
        <div
          style={{
            height: "100%",
            width: "25%",
            padding: "6px",
          }}
        >
          <div
            style={{
              backgroundImage: `url(${pic?.value?.value})`,
              backgroundRepeat: "no-repeat",
              backgroundSize: "100% 100%",
              width: "100%",
              height: "100%",
              borderRadius: "6px",
              display: "flex",
              flexDirection: "column",
              justifyContent: "flex-end",
              alignItems: "center",
              overflow: "hidden",
            }}
            onClick={() => {
              if (link?.value?.value) {
                if (
                  link?.value?.value.startsWith("http://") ||
                  link?.value?.value.startsWith("https://")
                ) {
                  window.location.href = link?.value?.value;
                } else {
                  history.push(link?.value?.value);
                }
              }
            }}
          >
            <div
              style={{
                width: "100%",
                height: 25,
                backgroundColor: "rgba(0,0,0,0.3)",
                color: "white",
                textAlign: "center",
                lineHeight: "25px",
              }}
            >
              {title?.value?.value || ""}
            </div>
          </div>
        </div>
      );
    });
    return results;
  };

  renderPage = () => {
    const { data = [] } = this.props;
    let components = [];
    for (let i = 0; i < data?.length; i = i + pageSize) {
      components.push(
        <Swiper.Item key={i}>
          <div style={{ height: "88%", display: "flex" }}>
            {this.renderItem(i / pageSize)}
          </div>
        </Swiper.Item>
      );
    }
    return components;
  };

  render() {
    return (
      <div className="app-secondary" id={this.state.id}>
        {/**/}
        {/* {this.state.number}
        <button onClick={this.handleClick}></button> */}
        <div>
          <Swiper
            style={{ "--height": "130px" }}
            indicatorProps={{
              style: {
                "--dot-color": "rgba(0, 0, 0, 0.3)",
                "--active-dot-color": "rgba(0, 0, 0, 0.6)",
                "--dot-size": "10px",
                "--active-dot-size": "10px",
                "--dot-border-radius": "50%",
                "--active-dot-border-radius": "15px",
                "--dot-spacing": "8px",
              },
            }}
          >
            {this.renderPage()}
          </Swiper>
        </div>
      </div>
    );
  }
}
